export class ProductService {

    getProductsSmall() {
        return fetch(process.env.PUBLIC_URL + 'data/products-small.json').then(res => res.json()).then(d => d.data);
    }

    getProducts() {
        return fetch(process.env.PUBLIC_URL + '/data/products.json').then(res => res.json()).then(d => d.data);
    }

    getProductsWithOrdersSmall() {
        return fetch(process.env.PUBLIC_URL + '/data/products-orders-small.json').then(res => res.json()).then(d => d.data);
    }
}
    